.feature {
	padding: 0.8rem;
	padding-top: 0.4rem;
	border: 0;
}

.feature + .feature {
	margin-top: 0.8rem;
}

.feature__name {
	font-size: inherit;
	line-height: inherit;
	font-weight: bold;
}

.feature__item + .feature__item {
	margin-top: 0.8rem;
}

/* Hide radios from sighted users,
  while keeping them available to the keyboard */
.feature__option {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);
	padding: 0 !important;
	border: 0 !important;
	height: 1px !important;
	width: 1px !important;
	overflow: hidden;
}

.feature__label {
	display: block;
	padding: 0.8rem;
	border: 0.1rem solid #d6d6d6;
	border-radius: 0.4rem;
}

.feature__label:hover {
	border-color: #888;
}

.feature__option:checked + .feature__label {
	border: 0.2rem solid #0070c9;
}

.feature__option:focus + .feature__label {
	box-shadow: 0 0 0 0.1rem #fff, 0 0 0 0.3rem #83c0fd;
}

.feature__option:not(:checked) + .feature__label {
	cursor: pointer;
}
